import NiceModal, { useModal, antdModal } from "@ebay/nice-modal-react";
import { Form, Input, InputNumber, Modal, Select } from "antd";
import { useCallback, useState } from "react";
import { IMember, MEMBER_TYPES, MemberTypeEnum } from "./types";

const { Option } = Select;

export default NiceModal.create(({ member }: { member: IMember }) => {
  const [isOwner, setIsOwner] = useState(false);

  const modal = useModal();
  const [form] = Form.useForm();
  // const meta = {
  //   initialValues: user,
  //   fields: [
  //     { key: 'name', label: 'Name', required: true },
  //     { key: 'job', label: 'Job Title', required: true },
  //   ],
  // };

  const handleSubmit = useCallback(() => {
    form.validateFields().then(() => {
      const newMember = { ...form.getFieldsValue() };
      modal.resolve(newMember);
      modal.hide();
    });
  }, [
    modal,
    // user,
    form,
  ]);

  const handleChangeMemberType = (values: any[]) => {
    if (values.includes(MemberTypeEnum.owner)) {
      setIsOwner(true);
    } else {
      setIsOwner(false);
    }
  };

  // const a = form.getFieldValue('type')

  return (
    <Modal
      {...antdModal(modal)}
      title="–õ–∏—Ü–æ"
      //   okText={user ? "Update" : "Create"}
      onOk={handleSubmit}
      okText="–°–æ—Ö—Ä–∞–Ω–∏—Ç—å"
      cancelText="–û—Ç–º–µ–Ω–∞"
    >
      <Form form={form} initialValues={member} layout="vertical">
        <Form.Item
          name="firstname"
          label="–ò–º—è"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name="lastname"
          label="–û—Ç—á–µ—Å—Ç–≤–æ"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name="sirname"
          label="–§–∞–º–∏–ª–∏—è"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name="passport"
          label="–ü–∞—Å–ø–æ—Ä—Ç–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name="type"
          label="–†–æ–ª—å —É—á–∞—Å—Ç–Ω–∏–∫–∞"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <Select
            mode="multiple"
            allowClear
            style={{ width: "100%" }}
            // placeholder="Please select"
            // defaultValue={["a10", "c12"]}
            onChange={handleChangeMemberType}
          >
            {MEMBER_TYPES.map((member_type) => (
              <Option key={member_type.en}>{member_type.ru}</Option>
            ))}
          </Select>
        </Form.Item>
        {isOwner && (
          <Form.Item
            name="part"
            label="–î–æ–ª—è –≤ –∫–æ–º–ø–∞–Ω–∏–∏, %"
            rules={[
              {
                required: true,
                message: "Please input the title of collection!",
              },
            ]}
          >
            <InputNumber min={0} max={100} />
          </Form.Item>
        )}
      </Form>
    </Modal>
  );
});
